import { Component, EventEmitter } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  gettext,
  ISelectModalObject,
  ISelectModalOption,
  ModalLabels,
  ModalSelectionMode
} from '@c8y/ngx-components';
import { get } from 'lodash-es';
import { IManagedObject, QueriesUtil } from '@c8y/client';
import { Subject, Observable, from, BehaviorSubject } from 'rxjs';
import { switchMap, map, mergeMap, shareReplay, tap } from 'rxjs/operators';
import { RepositoryService } from '../repository.service';
import {
  RepositoryType,
  RepositoryCategory,
  RepositoryBinary,
  SelectedRepositoryBinary
} from '../repository.model';

// MODAL STRUCTURE
// - selectModalObject (repository entry (repositoryCategory) -> type c8y_Firmware/c8y_Software)
//   -- ISelectModalOption (repository binary entry (repositoryBinary) => type c8y_FirmwareBinary/c8y_SoftwareBinary)
//   -- ISelectModalOption...
// - selectModalObject...

/**
 * RepositorySelectModalComponent displays repository entries options and allows to select them.
 *
 * @example
 * ```
 * import { take } from 'rxjs/operators';
 * import { RepositorySelectModalComponent, ModalSelectionMode, RepositoryType } from '@c8y/ngx-components/repository';
 *
 * const initialState = {
 *   repositoryType: RepositoryType.FIRMWARE,
 *   title: gettext('Install firmware'),
 *   subTitle: gettext('Available firmwares matching the device type'),
 *   icon: 'c8y-firmware',
 *   mode: ModalSelectionMode.SINGLE,
 *   labels: { ok: gettext('Install') },
 *   disableSelected: false
 * };
 *
 * const modal = this.bsModal.show(RepositorySelectModalComponent, {
 *   ignoreBackdropClick: true,
 *   initialState
 * });
 *
 * modal.content.load.next();
 * modal.content.resultEmitter.pipe(take(1)).subscribe((firmware) => {
 *   ...
 * })
 * ```
 */

@Component({
  selector: 'c8y-repository-select-modal',
  templateUrl: './repository-select-modal.component.html'
})
export class RepositorySelectModalComponent {
  /**
   * Optional
   * Allows to provide custom data.
   * @example
   * ```
   * import { from } from 'rxjs';
   *
   * const repositoryEntry = { name: 'ExampleEntry', type: 'c8y_Firmware' };
   * const versions = [{ c8y_Firmware: { version: '1.0.0', url: 'http://example.com' } }];
   *
   * const initialState = {repositoryEntriesWithVersions$: from({ ...repositoryEntry, versions })};
   * ```
   */
  repositoryEntriesWithVersions$: Observable<IManagedObject[]> = undefined;
  /**
   * Repository entry type.
   */
  repositoryType: RepositoryType.FIRMWARE | RepositoryType.SOFTWARE;
  /**
   * Optional
   * Allows to use custom badges templates.
   * @example
   * ```
   * import { gettext } from '@c8y/ngx-components';
   *
   * const badgeTemplates = { '=1': gettext('{{count}} version'), other: gettext('{{count}} versions') };
   * const initialState = { badgeTemplates };
   * ```
   */
  badgeTemplates = { '=1': gettext('{{count}} version'), other: gettext('{{count}} versions') };
  /**
   * Optional
   * Allows to provide custom modal title.
   */
  title: string = gettext('Select repository entry');
  /**
   * Optional
   * Allows to provide custom modal subtitle.
   */
  subTitle: string;
  /**
   * Loads the content of the modal.
   * Must be invoked by the modal's caller.
   */
  load: Subject<void> = new Subject();
  /**
   * Optional
   * Emits a search string currently entered in the filter input.
   * Use it to filter the items if you use custom repositoryEntriesWithVersions$.
   */
  searchTerm: BehaviorSubject<string> = new BehaviorSubject('');
  /**
   * Optional
   * Allows to provide device type query to restrict search criteria.
   * Only takes effect when repositoryEntriesWithVersions$ is not provided,
   * otherwise modal's caller have to provide already filtered data in the repositoryEntriesWithVersions$.
   */
  deviceTypeQuery: any = {};
  /**
   * Optional
   * Allows to provide query to restrict search criteria.
   * Only takes effect when repositoryEntriesWithVersions$ is not provided,
   * otherwise modal's caller have to provide already filtered data in the repositoryEntriesWithVersions$.
   */
  searchQuery: any = {};
  /**
   * Optional
   * Allows to provide custom labels for the buttons responsible for confirm/dismiss modal actions.
   */
  labels: ModalLabels = { ok: gettext('Save') };
  /**
   * Optional
   * Allows to hide the filter input field.
   * By default, the filter input field is displayed.
   */
  showFilter: boolean = true;
  /**
   * Optional
   * Allows to show a warning that the search criteria should be narrowed down.
   * By default, this warning is hidden.
   */
  areMoreEntries: boolean = false;
  /**
   * Optional
   * Allows to pass the array of items. Each item from this array will be marked as selected in the modal.
   */
  selected: SelectedRepositoryBinary[];
  /**
   * Emits the list of selected options.
   */
  resultEmitter: EventEmitter<SelectedRepositoryBinary[]> = new EventEmitter<
    SelectedRepositoryBinary[]
  >();
  /**
   * Optional
   * Allows to change selection mode.
   * Supported options:
   *   * single: only single option can be selected.
   *   * multiple: multiple options can be selected.
   */
  mode: ModalSelectionMode = ModalSelectionMode.SINGLE;
  /**
   * Optional
   * Allows to use custom icon in the modal header.
   */
  icon: string;
  /**
   * Allows to block selection of the other versions from the same repository entry.
   */
  disableSelected: boolean = true;
  modalEntries: Observable<ISelectModalObject[]> = this.load.pipe(
    switchMap(() => this.repositoryEntriesWithVersions$),
    mergeMap(mos => this.aggregate(mos)),
    tap(items => {
      this.areMoreEntries = items.length >= this.PAGE_SIZE ? true : false;
    })
  );
  private PAGE_SIZE = 100;
  private queriesUtil: QueriesUtil;

  constructor(
    private repositoryService: RepositoryService,
    private translateService: TranslateService
  ) {
    this.queriesUtil = new QueriesUtil();
  }

  ngOnInit() {
    if (!this.repositoryType) {
      throw new Error('Repository type must be defined');
    }

    if (!this.repositoryEntriesWithVersions$) {
      this.repositoryEntriesWithVersions$ = from(
        this.repositoryService.listRepositoryEntries(this.repositoryType, {
          query: this.queriesUtil.addOrFilter(this.deviceTypeQuery, this.searchQuery),
          params: { pageSize: this.PAGE_SIZE }
        })
      ).pipe(
        map(({ data }) => data),
        map(mos => this.getAndAssignRepositoryBinaries(mos)),
        shareReplay(1)
      );
    }
  }

  getAndAssignRepositoryBinaries(mos: IManagedObject[]) {
    mos.forEach(mo => {
      mo.versions = this.repositoryService.listAllVersions(mo);
    });
    return mos;
  }

  search(searchTerm: string) {
    this.searchTerm.next(searchTerm);
    if (!searchTerm) {
      this.searchQuery = {};
    } else {
      this.searchQuery = { name: `*${searchTerm}*` };
    }
    this.load.next();
  }

  result(selectedItems: SelectedRepositoryBinary[]) {
    this.resultEmitter.emit(selectedItems);
  }

  async aggregate(mos: IManagedObject[]): Promise<ISelectModalObject[]> {
    const repositoryType = this.repositoryType;
    const selectedItems: SelectedRepositoryBinary[] = this.selected;

    return Promise.all(
      mos.map(async repositoryEntry => {
        const options: ISelectModalOption[] = this.getSelectModalOptions(
          await this.repositoryService.fetchAllItemsFromList(repositoryEntry.versions),
          selectedItems,
          repositoryEntry as RepositoryCategory,
          repositoryType
        );
        const selectModalObject = this.getSelectModalObject(
          repositoryEntry as RepositoryCategory,
          options
        );

        return selectModalObject;
      })
    );
  }

  getSelectModalOptions(
    versions: RepositoryBinary[],
    selectedItems: SelectedRepositoryBinary[],
    repositoryEntry: RepositoryCategory,
    repositoryType: RepositoryType
  ): ISelectModalOption[] {
    const selectModalOptions: ISelectModalOption[] = [];
    versions.forEach(repositoryBinary => {
      const isSelected: boolean = this.isBinaryRepositorySelected(
        selectedItems,
        repositoryEntry,
        repositoryBinary,
        repositoryType
      );

      const { version } = repositoryBinary[`${repositoryType}`];
      const bodyValue =
        version || `(${this.translateService.instant(gettext('not specified`version`'))})`;
      const bodyClass = version ? '' : 'text-muted';
      selectModalOptions.push({
        body: [
          {
            value: bodyValue,
            class: bodyClass
          }
        ],
        obj: {
          id: repositoryBinary.id,
          name: repositoryEntry.name,
          version,
          ...(get(repositoryBinary, 'c8y_Patch.dependency') && {
            dependency: get(repositoryBinary, 'c8y_Patch.dependency')
          }),
          ...(get(repositoryBinary, 'c8y_Patch') && { c8y_Patch: true }),
          url: repositoryBinary[`${repositoryType}`].url
        },
        selected: isSelected
      });
    });
    return selectModalOptions;
  }

  isBinaryRepositorySelected(
    selectedItems: SelectedRepositoryBinary[],
    repositoryEntry: RepositoryCategory,
    repositoryBinary: RepositoryBinary,
    repositoryType: RepositoryType
  ): boolean {
    const isSelected = selectedItems
      ? selectedItems.filter(
          repositoryFragment =>
            repositoryFragment.name === repositoryEntry.name &&
            repositoryFragment.version === repositoryBinary[`${repositoryType}`].version
        ).length > 0
      : false;

    return isSelected;
  }

  getSelectModalObject(
    repositoryEntry: RepositoryCategory,
    options: ISelectModalOption[]
  ): ISelectModalObject {
    const label =
      options.length === 1
        ? this.translateService.instant(this.badgeTemplates['=1'], { count: options.length })
        : this.translateService.instant(this.badgeTemplates.other, { count: options.length });

    const selectModalObject: ISelectModalObject = {
      groupId: repositoryEntry.id,
      body: [
        { value: repositoryEntry.name, class: 'text-truncate' },
        { value: repositoryEntry.description, class: 'text-truncate text-muted' }
      ],
      additionalInformation: { value: label, class: 'label label-info' },
      options
    };

    return selectModalObject;
  }
}
