import { Pipe, PipeTransform } from '@angular/core';
import { SupportedConfigurationItem } from '../repository.model';

@Pipe({
  name: 'configurationFilterPipe'
})
export class ConfigurationFilterPipe implements PipeTransform {
  transform(items: any[], filterTerm: string): any {
    return filterTerm.trim().length === 0
      ? items
      : items.filter(
          (item: SupportedConfigurationItem) =>
            this.filterContainString(item.name, filterTerm) ||
            this.filterContainString(item.deviceType, filterTerm)
        );
  }

  private filterContainString(name: string, filterTerm: string) {
    const term = filterTerm.toLowerCase().trim();
    return name && name.toLowerCase().indexOf(term) > -1;
  }
}
