import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ConfigurationSnapshot, DeviceConfigurationOperation } from '../repository.model';
import {
  IManagedObject,
  IOperation,
  OperationService,
  OperationStatus,
  Realtime,
  UserService
} from '@c8y/client';
import { DeviceConfigurationService } from './device-configuration.service';
import { Subscription } from 'rxjs';
import { saveAs } from 'file-saver/FileSaver';
import { BsModalService } from 'ngx-bootstrap/modal';
import { SaveToRepositoryComponent } from './save-to-repository.component';
import { cloneDeep } from 'lodash-es';
import { AlertService, AppStateService } from '@c8y/ngx-components';
import { RepositoryService } from '../repository.service';

@Component({
  selector: 'c8y-device-configuration-preview',
  templateUrl: './configuration-preview.component.html'
})
export class ConfigurationPreviewComponent implements OnInit, OnDestroy {
  @Input() device: IManagedObject;
  @Input() set configurationType(type: string) {
    this._configurationType = type;
    this.setOperation(type);
  }
  get configurationType(): string {
    return this._configurationType;
  }
  @Input() configSnapshot: ConfigurationSnapshot;
  @Input() canSaveSnapshot: boolean;
  @Input() actionButtonText: string;
  @Input() actionButtonIcon: string;
  @Input() isLegacy: boolean = false;
  @Input() operationToTrigger:
    | DeviceConfigurationOperation.UPLOAD_CONFIG
    | DeviceConfigurationOperation.DOWNLOAD_CONFIG;

  operation: IOperation;
  canCallAction = true;
  deviceConfigurationOperation = DeviceConfigurationOperation;
  private _configurationType: string;
  private operationsSubscription: Subscription;

  constructor(
    private deviceConfigurationService: DeviceConfigurationService,
    private realtime: Realtime,
    private bsModal: BsModalService,
    private user: UserService,
    private appState: AppStateService,
    private repositoryService: RepositoryService,
    private operationService: OperationService,
    private alertService: AlertService
  ) {}

  async ngOnInit() {
    this.setOperation(this._configurationType);
    const operationsChannel = `/operations/${this.device.id}`;
    this.operationsSubscription = this.realtime
      .observable(operationsChannel)
      .subscribe(({ data }) => {
        this.updatePreview(data);
      });
    if (this.isLegacy) {
      this.canCallAction = this.deviceConfigurationService.hasAnySupportedOperation(
        this.device,
        this.operationToTrigger
      );
    }
  }

  async setOperation(configType): Promise<void> {
    const operationList = await this.repositoryService.getConfigFileOperationList(
      this.device.id,
      this.operationToTrigger
    );

    const operation = this.isLegacy
      ? operationList.find(op => op[this.operationToTrigger] && !op[this.operationToTrigger].type)
      : operationList.find(op => op[this.operationToTrigger].type === configType);

    this.operation =
      operation && operation.status !== OperationStatus.SUCCESSFUL ? operation : undefined;
  }

  async createDeviceOperation() {
    let operationCfg;
    if (this.operationToTrigger === DeviceConfigurationOperation.DOWNLOAD_CONFIG) {
      operationCfg = this.repositoryService.getDownloadConfigurationFileOperation(
        this.device,
        this._configurationType,
        this.configSnapshot,
        this.isLegacy
      );
    }
    if (this.operationToTrigger === DeviceConfigurationOperation.UPLOAD_CONFIG) {
      operationCfg = this.repositoryService.getUploadConfigurationFileOperation(
        this.device,
        this._configurationType,
        this.isLegacy
      );
    }
    try {
      this.operation = (await this.operationService.create(operationCfg)).data;
    } catch (ex) {
      this.alertService.addServerFailure(ex);
    }
  }

  showOperation(): boolean {
    if (this.operationToTrigger === DeviceConfigurationOperation.DOWNLOAD_CONFIG) {
      return !!this.operation;
    }
    return (
      this.operation &&
      [OperationStatus.PENDING, OperationStatus.EXECUTING, OperationStatus.FAILED].includes(
        this.operation.status
      )
    );
  }

  showBinary(): boolean {
    if (this.operationToTrigger === DeviceConfigurationOperation.DOWNLOAD_CONFIG) {
      return true;
    }
    return !this.showOperation();
  }

  isCreateOperationDisabled(): boolean {
    return (
      this.operation &&
      [OperationStatus.PENDING, OperationStatus.EXECUTING].includes(this.operation.status)
    );
  }

  updatePreview(operation: IOperation) {
    if (
      operation &&
      operation[this.operationToTrigger] &&
      (this.isLegacy ||
        (operation[this.operationToTrigger].type &&
          operation[this.operationToTrigger].type === this.configurationType))
    ) {
      this.operation = operation;
      this.updateSnapshotsOnConfigUpload(operation);
    }
  }

  download() {
    const blob = new Blob([this.configSnapshot.binary], { type: this.configSnapshot.binaryType });
    let fileName = this.configSnapshot.name;
    switch (this.configSnapshot.binaryType) {
      case 'text/csv':
      case 'application/csv':
        fileName = fileName.concat('.csv');
        break;
      case 'text/yaml':
      case 'application/x-yaml':
        fileName = fileName.concat('.yaml');
        break;
      case 'application/json':
        fileName = fileName.concat('.json');
        break;
    }
    saveAs(blob, fileName);
  }

  async saveToRepository() {
    const initialState = {
      configSnapshot: cloneDeep(this.configSnapshot)
    };
    const modal = this.bsModal.show(SaveToRepositoryComponent, {
      class: 'modal-sm',
      initialState,
      ignoreBackdropClick: true
    }).content as SaveToRepositoryComponent;
    try {
      await modal.result;
      this.deviceConfigurationService.updateConfigurations(true);
      modal.close();
    } catch (ex) {
      // do nothing
    }
  }

  hasPermission(): boolean {
    return this.user.hasAnyRole(this.appState.currentUser.value, [
      'ROLE_INVENTORY_ADMIN',
      'ROLE_INVENTORY_CREATE'
    ]);
  }

  ngOnDestroy() {
    this.operationsSubscription.unsubscribe();
  }

  private async updateSnapshotsOnConfigUpload(operation) {
    if (
      operation[DeviceConfigurationOperation.UPLOAD_CONFIG] &&
      operation.status === OperationStatus.SUCCESSFUL
    ) {
      this.deviceConfigurationService.updateConfigurations();
    }
  }
}
