import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IManagedObject, Realtime } from '@c8y/client';
import { DeviceConfigurationService } from './device-configuration.service';
import {
  DeviceConfigurationListEmptyState,
  ConfigurationSnapshot,
  SupportedConfigurationItem,
  RepositoryType,
  DeviceConfigurationOperation
} from '../repository.model';
import { gettext } from '@c8y/ngx-components';
import { RepositoryService } from '../repository.service';

@Component({
  selector: 'c8y-device-configuration',
  templateUrl: './device-configuration.component.html'
})
export class DeviceConfigurationComponent implements OnInit {
  supportedConfigurations: SupportedConfigurationItem[] = [];
  configurationType: string;
  configSnapshot: Partial<ConfigurationSnapshot> = {};
  repositorySnapshots: IManagedObject[];
  repositorySnapshot: ConfigurationSnapshot;
  repositorySnapshotsEmptyState: DeviceConfigurationListEmptyState;
  device: IManagedObject;
  isLegacy: boolean;
  reloading: boolean = false;

  constructor(
    private route: ActivatedRoute,
    private deviceConfigurationService: DeviceConfigurationService,
    private realtime: Realtime,
    private repositoryService: RepositoryService
  ) {
    this.deviceConfigurationService.configurationsUpdated.subscribe(repositorySnapsOnly => {
      this.updateSnapshots(repositorySnapsOnly);
    });
  }

  ngOnInit() {
    this.device = this.route.snapshot.parent.data.contextData;
    if (this.device.c8y_SupportedConfigurations) {
      this.supportedConfigurations = this.device.c8y_SupportedConfigurations.map(item => ({
        name: item
      }));
    }

    if (
      this.deviceConfigurationService.hasAnySupportedOperation(this.device, [
        DeviceConfigurationOperation.DOWNLOAD_CONFIG,
        DeviceConfigurationOperation.UPLOAD_CONFIG
      ])
    ) {
      this.supportedConfigurations.push({
        name: gettext('Legacy configuration snapshot'),
        isLegacy: true
      });
    }

    this.repositorySnapshotsEmptyState = {
      icon: 'gears',
      title: gettext('No configurations available.'),
      text: gettext('Add configuration to configuration repository')
    };
  }

  async onConfigTypeSelected(config) {
    this.configurationType = config.name;
    this.isLegacy = config.isLegacy;
    this.updateSnapshots();
  }

  async onRepositoryConfigSelected(config) {
    this.repositorySnapshot = {
      id: config.id,
      time: config.creationTime,
      name: config.name,
      binaryUrl: config.url,
      deviceType: config.deviceType,
      configurationType: config.configurationType
    };
    if (config.url) {
      try {
        const binary = await this.repositoryService.getBinaryFile(config.url, {
          allowExternal: false
        });
        if (binary) {
          this.repositorySnapshot.binary = await (binary as any).text();
        }
      } catch (ex) {
        // do nothing
      }
    }
  }

  async updateSnapshots(repositorySnapsOnly?: boolean) {
    this.reloading = true;
    this.repositorySnapshot = undefined;
    this.repositorySnapshots = await this.getSnapshotsFromRepository(
      this.device,
      this.configurationType
    );
    if (!repositorySnapsOnly) {
      this.configSnapshot = this.isLegacy
        ? await this.repositoryService.getLegacyConfigSnapshot(this.device)
        : await this.repositoryService.getConfigSnapshot(this.device, this.configurationType);
    }
    this.reloading = false;
  }

  private async getSnapshotsFromRepository(device, configurationType) {
    const searchQuery = this.repositoryService.getConfigurationTypeQuery(device, configurationType);
    const res = await this.repositoryService.listRepositoryEntries(RepositoryType.CONFIGURATION, {
      query: searchQuery,
      params: { pageSize: 100 }
    });
    return res.data;
  }
}
