import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DeviceConfigurationListEmptyState, SupportedConfigurationItem } from '../repository.model';

@Component({
  selector: 'c8y-device-configuration-list',
  templateUrl: './device-configuration-list.component.html'
})
export class DeviceConfigurationListComponent {
  @Input() items: SupportedConfigurationItem[];
  @Input() itemIcon: string;
  @Input() emptyState: DeviceConfigurationListEmptyState;
  @Input() isFilterEnabled: boolean;
  @Output() configSelected = new EventEmitter<string>();
  selectedConfig;
  filterTerm: string = '';

  showConfigurationTypePreview(config): void {
    this.selectedConfig = config;
    this.configSelected.emit(config);
  }

  updatePipe(filterTerm: string) {
    this.filterTerm = filterTerm;
  }
}
