import { EventEmitter, Injectable } from '@angular/core';
import { DeviceConfigurationOperation } from '../repository.model';
import { IManagedObject } from '@c8y/client';

@Injectable()
export class DeviceConfigurationService {
  configurationsUpdated = new EventEmitter<any>();

  updateConfigurations(repositorySnapsOnly?: boolean) {
    this.configurationsUpdated.emit(repositorySnapsOnly);
  }

  hasAnySupportedOperation(
    mo: IManagedObject,
    operation: DeviceConfigurationOperation | DeviceConfigurationOperation[]
  ) {
    const supported = mo.c8y_SupportedOperations as [];
    if (!supported) {
      return false;
    }
    if (!Array.isArray(operation)) {
      operation = [operation];
    }
    return supported.some(supportedOperation => operation.includes(supportedOperation));
  }
}
