import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';

@Injectable()
export class RepositoryNavigationFactory implements NavigatorNodeFactory {
  navs: NavigatorNode[] = [];

  async get() {
    if (this.navs.length === 0) {
      this.navs.push(
        new NavigatorNode({
          label: gettext('Firmware repository'),
          path: 'firmware',
          icon: 'c8y-firmware',
          parent: gettext('Management'),
          priority: 1000
        })
      );
      this.navs.push(
        new NavigatorNode({
          label: gettext('Software repository'),
          path: 'software',
          icon: 'c8y-tools',
          parent: gettext('Management'),
          priority: 900
        })
      );
      this.navs.push(
        new NavigatorNode({
          label: gettext('Configuration repository'),
          path: 'configuration',
          icon: 'gears',
          parent: gettext('Management'),
          priority: 800
        })
      );
    }
    return this.navs;
  }
}
