import { Router, ActivatedRoute } from '@angular/router';
import { Component } from '@angular/core';
import { BsModalService, ModalOptions } from 'ngx-bootstrap/modal';
import { TranslateService } from '@ngx-translate/core';
import {
  distinctUntilChanged,
  tap,
  debounceTime,
  switchMap,
  shareReplay,
} from 'rxjs/operators';
import { Observable, BehaviorSubject, combineLatest } from 'rxjs';
import { IResultList, IManagedObject } from '@c8y/client';
import { ModalService, gettext, Status, AlertService, memoize } from '@c8y/ngx-components';
import { RepositoryService } from '../repository.service';
import { RepositoryType } from '../repository.model';
import { AddSoftwareModalComponent } from './add-software-modal.component';
import { property, get } from 'lodash-es';

@Component({
  selector: 'c8y-software-list',
  templateUrl: 'software-list.component.html'
})
export class SoftwareListComponent {
  textFilter$: BehaviorSubject<string> = new BehaviorSubject('');
  reload$: BehaviorSubject<void> = new BehaviorSubject(null);
  reloading: boolean = false;
  softwares$: Observable<IResultList<IManagedObject>> = combineLatest(
    this.textFilter$.pipe(
      debounceTime(400),
      distinctUntilChanged()
    ),
    this.reload$
  ).pipe(
    tap(() => {
      this.reloading = true;
    }),
    switchMap(([text]) => this.getSoftwares(text)),
    tap(() => {
      this.reloading = false;
    }),
    shareReplay(1)
  );
  isLegacy = this.repositoryService.isLegacyEntry.bind(this.repositoryService);
  readonly DEVICE_TYPE_NOT_AVAILABLE: string = gettext('No device type available');

  constructor(
    private repositoryService: RepositoryService,
    private modalService: ModalService,
    private bsModalService: BsModalService,
    private translateService: TranslateService,
    private alertService: AlertService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {}

  getSoftwares(partialName?: string) {
    return this.repositoryService.listRepositoryEntries(RepositoryType.SOFTWARE, { partialName });
  }

  addSoftware() {
    const config: ModalOptions = { class: 'modal-sm', ignoreBackdropClick: true };
    const modalRef = this.bsModalService.show(AddSoftwareModalComponent, config);
    modalRef.content.saved.subscribe(savedSoftware => this.editSoftware(savedSoftware));
  }

  editSoftware(software: IManagedObject) {
    this.router.navigate([software.id], { relativeTo: this.activatedRoute });
  }

  async deleteSoftware(software: IManagedObject) {
    try {
      const title = gettext('Delete software');
      const body = `
        ${this.translateService.instant(
          gettext('You are about to delete software "{{ name }}" with all its versions.'),
          { name: software.name }
        )}
        ${this.translateService.instant(gettext('This operation is irreversible.'))}
        ${this.translateService.instant(gettext('Do you want to proceed?'))}
      `;
      const labels = {
        ok: gettext('Delete')
      };
      await this.modalService.confirm(title, body, Status.DANGER, labels);
      await this.repositoryService.delete(software);
      this.alertService.success(gettext('Software deleted.'));
      this.reload$.next();
    } catch (ex) {
      // only if not cancel from modal
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  @memoize(property('id'))
  getBaseVersionsCount$(software: IManagedObject): Observable<number | string> {
    return this.softwares$.pipe(
      switchMap(() => this.repositoryService.getBaseVersionsCount$(software)),
      shareReplay(1)
    );
  }

  getDeviceTypeTitle(software: IManagedObject): string {
    return get(
      software,
      'c8y_Filter.type',
      this.translateService.instant(this.DEVICE_TYPE_NOT_AVAILABLE));
  }
}
