import { BsModalService, ModalOptions } from 'ngx-bootstrap';
import { AlertService, gettext, memoize, ModalService, Status } from '@c8y/ngx-components';
import { Observable, Subject, merge, BehaviorSubject } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { RepositoryService } from '../repository.service';
import { IManagedObject } from '@c8y/client';
import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  map,
  switchMap,
  takeUntil,
  withLatestFrom,
  tap,
  take,
  distinctUntilKeyChanged,
  shareReplay
} from 'rxjs/operators';
import { InventoryService, IResultList } from '@c8y/client';
import { TranslateService } from '@ngx-translate/core';
import { AddSoftwareModalComponent } from './add-software-modal.component';

@Component({
  selector: 'c8y-software-details',
  templateUrl: './software-details.component.html'
})
export class SoftwareDetailsComponent implements OnInit, OnDestroy {
  reload$: Subject<void> = new Subject();
  reloading$: BehaviorSubject<boolean> = new BehaviorSubject(false);

  updateSoftware$: Subject<Partial<IManagedObject>> = new Subject();
  softwareUpdated$: Subject<IManagedObject> = new Subject();
  baseVersionsUpdated$: Subject<void> = new Subject();

  software$: Observable<IManagedObject> = merge(
    this.activatedRoute.params.pipe(
      map(params => params.id),
      switchMap(id => this.inventoryService.detail$(id))
    ),
    this.reload$.pipe(
      tap(() => this.reloading$.next(true)),
      switchMap(() => this.activatedRoute.params),
      map(params => params.id),
      switchMap(id => this.inventoryService.detail$(id)),
      tap(() => this.reloading$.next(false))
    ),
    this.softwareUpdated$
  ).pipe(shareReplay(1));

  baseVersions$: Observable<IResultList<IManagedObject>> = merge(
    this.software$.pipe(distinctUntilKeyChanged('id')),
    this.baseVersionsUpdated$,
    this.reload$
  ).pipe(
    switchMap(() => this.software$),
    switchMap(software => this.repositoryService.listBaseVersions(software)),
    shareReplay(1)
  );

  isLegacy$: Observable<boolean> = this.software$.pipe(
    map(software => this.repositoryService.isLegacyEntry(software)),
    shareReplay(1)
  );

  destroy$: Subject<boolean> = new Subject<boolean>();

  constructor(
    private activatedRoute: ActivatedRoute,
    private inventoryService: InventoryService,
    private repositoryService: RepositoryService,
    private alertService: AlertService,
    private translateService: TranslateService,
    private modalService: ModalService,
    private bsModalService: BsModalService
  ) {}

  ngOnInit() {
    this.updateSoftware$
      .pipe(
        withLatestFrom(this.software$),
        switchMap(([softwarePartial, software]) =>
          this.inventoryService.update({
            id: software.id,
            ...softwarePartial
          })
        ),
        map(({ data }) => data),
        tap(software => this.softwareUpdated$.next(software)),
        tap(() => this.alertService.success(gettext('Saved.'))),
        takeUntil(this.destroy$)
      )
      .subscribe();
  }

  @memoize()
  getBinaryName$(binaryUrl) {
    return this.repositoryService.getBinaryName$(binaryUrl);
  }

  addBaseVersion() {
    this.software$
      .pipe(
        take(1),
        switchMap(software => {
          const initialState = {
            model: {
              selected: software,
              description: software.description
            }
          };
          const config: ModalOptions = {
            class: 'modal-sm',
            ignoreBackdropClick: true,
            initialState
          };
          const modalRef = this.bsModalService.show(AddSoftwareModalComponent, config);
          return modalRef.content.saved;
        })
      )
      .subscribe(() => this.baseVersionsUpdated$.next());
  }

  async deleteBaseVersion(baseVersion: IManagedObject) {
    try {
      const title = gettext('Delete software');
      const body = `
        ${this.translateService.instant(
          gettext('You are about to delete software {{ version }}.'),
          { version: baseVersion.c8y_Software.version }
        )}
        ${this.translateService.instant(gettext('This operation is irreversible.'))}
        ${this.translateService.instant(gettext('Do you want to proceed?'))}
      `;
      const labels = {
        ok: gettext('Delete')
      };
      await this.modalService.confirm(title, body, Status.DANGER, labels);
      await this.repositoryService.delete(baseVersion);
      this.alertService.success(gettext('Software deleted.'));
      this.baseVersionsUpdated$.next();
    } catch (ex) {
      // only if not cancel from modal
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }
}
