import { BsModalService, ModalOptions } from 'ngx-bootstrap';
import { AlertService, gettext, memoize, ModalService, Status } from '@c8y/ngx-components';
import { Observable, Subject, merge, BehaviorSubject, combineLatest } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { RepositoryService } from './../repository.service';
import { IManagedObject } from '@c8y/client';
import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  map,
  switchMap,
  takeUntil,
  withLatestFrom,
  tap,
  take,
  distinctUntilKeyChanged,
  shareReplay
} from 'rxjs/operators';
import { InventoryService, IResultList } from '@c8y/client';
import { TranslateService } from '@ngx-translate/core';
import { AddFirmwareModalComponent } from './add-firmware-modal.component';
import { AddFirmwarePatchModalComponent } from './add-firmware-patch-modal.component';
import { property } from 'lodash-es';
import { FirmwareBinary } from '../repository.model';

@Component({
  selector: 'c8y-firmware-details',
  templateUrl: './firmware-details.component.html'
})
export class FirmwareDetailsComponent implements OnInit, OnDestroy {
  reload$: Subject<void> = new Subject();
  reloading$: BehaviorSubject<boolean> = new BehaviorSubject(false);

  updateFirmware$: Subject<Partial<IManagedObject>> = new Subject();
  firmwareUpdated$: Subject<IManagedObject> = new Subject();
  baseVersionsUpdated$: Subject<void> = new Subject();
  patchVersionsUpdated$: Subject<void> = new Subject();

  firmware$: Observable<IManagedObject> = merge(
    this.activatedRoute.params.pipe(
      map(params => params.id),
      switchMap(id => this.inventoryService.detail$(id))
    ),
    this.reload$.pipe(
      tap(() => this.reloading$.next(true)),
      switchMap(() => this.activatedRoute.params),
      map(params => params.id),
      switchMap(id => this.inventoryService.detail$(id)),
      tap(() => this.reloading$.next(false))
    ),
    this.firmwareUpdated$
  ).pipe(shareReplay(1));

  baseVersions$: Observable<IResultList<IManagedObject>> = merge(
    this.firmware$.pipe(distinctUntilKeyChanged('id')),
    this.baseVersionsUpdated$,
    this.patchVersionsUpdated$,
    this.reload$
  ).pipe(
    switchMap(() => this.firmware$),
    switchMap(firmware => this.repositoryService.listBaseVersions(firmware)),
    shareReplay(1)
  );

  isLegacy$: Observable<boolean> = this.firmware$.pipe(
    map(firmware => this.repositoryService.isLegacyEntry(firmware)),
    shareReplay(1)
  );

  canAddPatchVersions$: Observable<boolean> = combineLatest(
    this.isLegacy$,
    this.baseVersions$.pipe(map(({ data }) => data.length > 0))
  ).pipe(map(([isLegacy, hasBaseVersions]) => !isLegacy && hasBaseVersions));

  expanded: { [id: string]: boolean } = {};

  destroy$: Subject<boolean> = new Subject<boolean>();

  constructor(
    private activatedRoute: ActivatedRoute,
    private inventoryService: InventoryService,
    private repositoryService: RepositoryService,
    private alertService: AlertService,
    private translateService: TranslateService,
    private modalService: ModalService,
    private bsModalService: BsModalService
  ) {}

  ngOnInit() {
    this.updateFirmware$
      .pipe(
        withLatestFrom(this.firmware$),
        switchMap(([firmwarePartial, firmware]) =>
          this.inventoryService.update({
            id: firmware.id,
            ...firmwarePartial
          })
        ),
        map(({ data }) => data),
        tap(firmware => this.firmwareUpdated$.next(firmware)),
        tap(() => this.alertService.success(gettext('Saved.'))),
        takeUntil(this.destroy$)
      )
      .subscribe();
  }

  @memoize(property('id'))
  getPatchVersionsCount$(baseVersion: FirmwareBinary) {
    return merge(
      this.firmware$.pipe(distinctUntilKeyChanged('id')),
      this.baseVersionsUpdated$,
      this.patchVersionsUpdated$,
      this.reload$
    ).pipe(
      switchMap(() => this.firmware$),
      switchMap(firmware => this.repositoryService.getPatchVersionsCount$(firmware, baseVersion)),
      shareReplay(1)
    );
  }

  @memoize()
  getBinaryName$(binaryUrl) {
    return this.repositoryService.getBinaryName$(binaryUrl);
  }

  @memoize(property('id'))
  getPatchVersions$(baseVersion) {
    return merge(
      this.firmware$.pipe(distinctUntilKeyChanged('id')),
      this.patchVersionsUpdated$,
      this.reload$
    ).pipe(
      switchMap(() => this.firmware$),
      switchMap(firmware => this.repositoryService.listPatchVersions(firmware, baseVersion)),
      shareReplay(1)
    );
  }

  addBaseVersion() {
    this.firmware$
      .pipe(
        take(1),
        switchMap(firmware => {
          const initialState = {
            model: {
              selected: firmware,
              description: firmware.description
            }
          };
          const config: ModalOptions = {
            class: 'modal-sm',
            ignoreBackdropClick: true,
            initialState
          };
          const modalRef = this.bsModalService.show(AddFirmwareModalComponent, config);
          return modalRef.content.saved;
        })
      )
      .subscribe(() => this.baseVersionsUpdated$.next());
  }

  addPatchVersion() {
    this.firmware$
      .pipe(
        take(1),
        switchMap(firmware => {
          const initialState = {
            model: {
              selected: firmware
            }
          };
          const config: ModalOptions = {
            class: 'modal-sm',
            ignoreBackdropClick: true,
            initialState
          };
          const modalRef = this.bsModalService.show(AddFirmwarePatchModalComponent, config);
          return modalRef.content.saved;
        })
      )
      .subscribe(() => this.patchVersionsUpdated$.next());
  }

  async deleteBaseVersion(baseVersion: IManagedObject) {
    try {
      const title = gettext('Delete firmware');
      const body = `
        ${this.translateService.instant(
          gettext('You are about to delete firmware {{ version }} with all its patches.'),
          { version: baseVersion.c8y_Firmware.version }
        )}
        ${this.translateService.instant(gettext('This operation is irreversible.'))}
        ${this.translateService.instant(gettext('Do you want to proceed?'))}
      `;
      const labels = {
        ok: gettext('Delete')
      };
      await this.modalService.confirm(title, body, Status.DANGER, labels);
      await this.repositoryService.delete(baseVersion);
      this.alertService.success(gettext('Firmware deleted.'));
      this.baseVersionsUpdated$.next();
    } catch (ex) {
      // only if not cancel from modal
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  async deletePatchVersion(patchVersion: IManagedObject) {
    try {
      const title = gettext('Delete firmware patch');
      const body = `
        ${this.translateService.instant(
          gettext('You are about to delete firmware patch {{ version }}.'),
          { version: patchVersion.c8y_Firmware.version }
        )}
        ${this.translateService.instant(gettext('This operation is irreversible.'))}
        ${this.translateService.instant(gettext('Do you want to proceed?'))}
      `;
      const labels = {
        ok: gettext('Delete')
      };
      await this.modalService.confirm(title, body, Status.DANGER, labels);
      await this.repositoryService.delete(patchVersion);
      this.alertService.success(gettext('Firmware patch deleted.'));
      this.patchVersionsUpdated$.next();
    } catch (ex) {
      // only if not cancel from modal
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }
}
