import { Component } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ConfigurationSnapshot, RepositoryType } from '../repository.model';
import { AlertService, gettext } from '@c8y/ngx-components';
import { RepositoryService } from '../repository.service';

@Component({
  selector: 'c8y-save-config-to-configuration-repository',
  templateUrl: 'save-to-repository.component.html'
})
export class SaveToRepositoryComponent {
  configSnapshot: ConfigurationSnapshot;
  result: Promise<any> = new Promise((resolve, reject) => {
    this._save = resolve;
    this._cancel = reject;
  });
  private _save;
  private _cancel;

  constructor(
    private modal: BsModalRef,
    private alertService: AlertService,
    private repositoryService: RepositoryService
  ) {}

  async save() {
    {
      try {
        const configSnapshotData = {
          selected: {
            configurationType: this.configSnapshot.configurationType
          },
          version: this.configSnapshot.name,
          deviceType: this.configSnapshot.deviceType,
          description: this.configSnapshot.description,
          binary: {
            file: new File([this.configSnapshot.binary], this.configSnapshot.name)
          }
        };
        await this.repositoryService.save(configSnapshotData, RepositoryType.CONFIGURATION);
        this.alertService.success(gettext('Configuration saved.'));
        this._save();
      } catch (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  close() {
    this._cancel();
    this.modal.hide();
  }
}
