import { Component, OnInit, Pipe, ViewChild } from '@angular/core';
import {IManagedObject, InventoryBinaryService, InventoryService, IResultList} from '@c8y/client';
import {
  AlertService,
  FilterInputComponent,
  gettext, memoize,
  ModalService,
  Status
} from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Observable, of, pipe } from 'rxjs';
import { map } from 'rxjs/operators';
import { ModalModel, RepositoryType } from '../repository.model';
import { RepositoryService } from '../repository.service';
import { ConfigurationDetailComponent } from './configuration-detail.component';
import { property } from 'lodash-es';
import { saveAs } from 'file-saver';

@Component({
  selector: 'c8y-configuration-list',
  templateUrl: './configuration-list.component.html'
})
export class ConfigurationListComponent implements OnInit {
  @ViewChild(FilterInputComponent, { static: false }) filter: FilterInputComponent;
  configurations$: Observable<IResultList<IManagedObject>>;
  filterPipe: Pipe;
  filterTerm = '';
  reloading: boolean = false;
  private readonly DELETED_SUCCESS_MSG = gettext('Configuration deleted.');

  constructor(
    private alert: AlertService,
    private repositoryService: RepositoryService,
    private bsModalService: BsModalService,
    private modalService: ModalService,
    private translateService: TranslateService,
    private inventoryBinaryService: InventoryBinaryService,
    private inventoryService: InventoryService
  ) {}

  ngOnInit() {
    this.loadConfigurations();
  }

  async loadConfigurations() {
    this.reloading = true;
    this.configurations$ = of(
      await this.repositoryService.listRepositoryEntries(RepositoryType.CONFIGURATION)
    );
    this.reloading = false;
    this.reset();
  }

  async add() {
    try {
      await (this.bsModalService.show(ConfigurationDetailComponent, {
        class: 'modal-sm',
        ignoreBackdropClick: true
      }).content as ConfigurationDetailComponent).result;
      await this.loadConfigurations();
    } catch (ex) {
      // intended empty
    }
  }

  async edit(configuration: IManagedObject) {
    const fileBinary: File = await this.repositoryService.getBinaryFile(configuration.url, {
      allowExternal: false
    });
    try {
      const modal = this.bsModalService.show(ConfigurationDetailComponent, {
        class: 'modal-sm',
        ignoreBackdropClick: true,
        initialState: {
          selected: { id: configuration.id, configurationType: configuration.configurationType },
          version: configuration.name,
          deviceType: configuration.deviceType,
          description: configuration.description,
          binary: { file: fileBinary, url: configuration.url }
        } as ModalModel
      }).content as ConfigurationDetailComponent;
      modal.mo = configuration;
      await modal.result;
      await this.loadConfigurations();
    } catch (ex) {
      // intended empty
    }
  }

  isBinaryFile(configuration: IManagedObject) {
    const id: string = this.inventoryBinaryService.getIdFromUrl(configuration.url);
    return id ? true : false;
  }

  @memoize(property('id'))
  getBinaryName(configuration: IManagedObject) {
    return this.repositoryService.getBinaryName$(configuration.url);
  }

  async download(configuration: IManagedObject) {
    const fileBinary: File = await this.repositoryService.getBinaryFile(configuration.url, {
      allowExternal: false
    });
    saveAs(fileBinary);
  }

  async delete(configuration: IManagedObject) {
    try {
      const title = gettext('Delete configuration snapshot');
      const confirmationText = gettext(
        'You are about to delete the configuration snapshot {{ name }}.'
      );
      const hint = gettext('This operation is irreversible.');
      const proceed = gettext('Do you want to proceed?');
      const body = [
        this.translateService.instant(confirmationText, {
          name: configuration.name
        }),
        this.translateService.instant(hint),
        this.translateService.instant(proceed)
      ].join(' ');
      const labels = {
        ok: gettext('Delete')
      };
      await this.modalService.confirm(title, body, Status.DANGER, labels);
      await this.repositoryService.delete(configuration);
      this.alert.success(this.DELETED_SUCCESS_MSG);
      await this.loadConfigurations();
    } catch (ex) {
      if (ex) {
        this.alert.addServerFailure(ex);
      }
    }
  }

  setPipe(filterTerm: string) {
    this.filterTerm = filterTerm;
    this.filterPipe = pipe(
      map((data: []) =>
        filterTerm.trim().length === 0
          ? data
          : data.filter(
              (mo: IManagedObject) =>
                this.filterContainString(mo.name, filterTerm) ||
                this.filterContainString(mo.configurationType, filterTerm) ||
                this.filterContainString(mo.deviceType, filterTerm)
            )
      )
    );
  }

  reset() {
    this.filter.filterTerm = '';
    this.setPipe('');
  }

  private filterContainString(name: string, filterTerm: string) {
    const term = filterTerm.toLowerCase().trim();
    return name && name.toLowerCase().indexOf(term) > -1;
  }
}
