import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { assign, isEmpty } from 'lodash-es';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BehaviorSubject, Observable, combineLatest, from, of } from 'rxjs';
import { distinctUntilChanged, filter, map, shareReplay, switchMap, take } from 'rxjs/operators';

import { ModalSelectionMode, gettext } from '@c8y/ngx-components';
import { IManagedObject, IOperation, InventoryService, OperationStatus } from '@c8y/client';

import { RepositoryService } from '../repository.service';
import { DeviceFirmware, FirmwareBinary, RepositoryType } from '../repository.model';
import { RepositorySelectModalComponent } from '../select-modal/repository-select-modal.component';

@Component({
  selector: 'c8y-firmware-device-tab',
  templateUrl: 'firmware-device-tab.component.html'
})
export class FirmwareDeviceTabComponent {
  device$: BehaviorSubject<IManagedObject> = new BehaviorSubject(
    this.route.parent.snapshot.data.contextData
  );
  reloading: boolean = false;
  deviceFirmwareFragment$: Observable<DeviceFirmware> = this.device$.pipe(
    map(device => device.c8y_Firmware)
  );
  firmwareBinary$: Observable<IManagedObject> = this.deviceFirmwareFragment$.pipe(
    filter(deviceFirmwareFragment => !isEmpty(deviceFirmwareFragment)),
    switchMap(deviceFirmwareFragment =>
      from(
        this.repository.getRepositoryBinaryMoByVersion(
          deviceFirmwareFragment,
          RepositoryType.FIRMWARE
        )
      )
    ),
    shareReplay(1)
  );
  repositoryEntry$: Observable<IManagedObject> = this.firmwareBinary$.pipe(
    switchMap(mo => this.repository.getRepositoryEntryMO$(mo)),
    shareReplay(1)
  );
  patches$: Observable<IManagedObject[]> = combineLatest(
    this.firmwareBinary$,
    this.repositoryEntry$
  ).pipe(
    switchMap(([firmwareBinary, repositoryEntry]) => {
      if (repositoryEntry && firmwareBinary) {
        const version: string = this.repository.getBaseVersionFromMO(
          firmwareBinary as FirmwareBinary
        );

        return from(this.repository.listPatchVersions(repositoryEntry, version)).pipe(
          map(({ data }) => data)
        );
      } else {
        return of([]);
      }
    }),
    shareReplay(1)
  );
  changesOperation$ = new BehaviorSubject<IOperation>(null);
  changesInProgress$: Observable<boolean> = this.changesOperation$.pipe(
    map(operation => this.isInProgress(operation))
  );

  constructor(
    private route: ActivatedRoute,
    private repository: RepositoryService,
    private inventory: InventoryService,
    private bsModal: BsModalService
  ) {}

  async ngOnInit() {
    // TODO check route snapshot, why is not refreshing device.
    // Scanario: missing deviceFirmwareFragment => install new version => switch tabs.
    // Expected: device should be set.
    await this.loadDevice();
    await this.loadOperation();
  }

  installFirmware() {
    const initialState = {
      repositoryEntriesWithVersions$: of([]),
      repositoryEntriesWithVersionsFn$: modal =>
        this.getRepositoryEntriesWithVersions$(modal.content.searchTerm),
      repositoryType: RepositoryType.FIRMWARE,
      title: gettext('Install firmware'),
      subTitle: gettext('Available firmwares matching the device type'),
      icon: 'c8y-firmware',
      mode: ModalSelectionMode.SINGLE,
      labels: { ok: gettext('Install') },
      disableSelected: false
    };

    this.deviceFirmwareFragment$
      .pipe(
        take(1),
        switchMap(deviceFirmwareFragment => {
          if (deviceFirmwareFragment) {
            const { name, version } = deviceFirmwareFragment;
            const selected = [{ name, version }];
            assign(initialState, { selected });
          }

          const modal = this.bsModal.show(RepositorySelectModalComponent, {
            ignoreBackdropClick: true,
            initialState
          });

          if (initialState.repositoryEntriesWithVersionsFn$) {
            modal.content.repositoryEntriesWithVersions$ = initialState.repositoryEntriesWithVersionsFn$(
              modal
            );
          }

          modal.content.load.next();

          return modal.content.resultEmitter;
        })
      )
      .subscribe(selectedOption => {
        this.handleOperation(selectedOption);
      });
  }

  getRepositoryEntriesWithVersions$(searchTerm$: BehaviorSubject<string>) {
    return searchTerm$.pipe(
      distinctUntilChanged(),
      switchMap(searchTerm =>
        this.repository.listRepositoryEntries(RepositoryType.FIRMWARE, {
          query: this.repository.getDeviceTypeQuery(RepositoryType.FIRMWARE, this.device$.value),
          partialName: searchTerm,
          params: { pageSize: 100 }
        })
      ),
      map(({ data }) => data),
      map(mos => this.getAndAssignRepositoryBinaries(mos)),
      shareReplay(1)
    );
  }

  getAndAssignRepositoryBinaries(mos: IManagedObject[]) {
    mos.forEach(mo => {
      mo.versions = this.repository.listBaseVersions(mo);
    });
    return mos;
  }

  addPatch() {
    const initialState = {
      repositoryType: RepositoryType.FIRMWARE,
      repositoryEntriesWithVersions$: this.getRepositoryEntryWithPatches$(),
      title: gettext('Install firmware'),
      subTitle: gettext('Available firmwares matching the device type'),
      icon: 'c8y-firmware',
      mode: ModalSelectionMode.SINGLE,
      labels: { ok: gettext('Install') },
      disableSelected: false
    };

    this.deviceFirmwareFragment$
      .pipe(
        take(1),
        switchMap(deviceFirmwareFragment => {
          if (deviceFirmwareFragment) {
            const { name, version } = deviceFirmwareFragment;
            const selected = [{ name, version }];
            assign(initialState, { selected });
          }

          const modal = this.bsModal.show(RepositorySelectModalComponent, {
            ignoreBackdropClick: true,
            initialState
          });
          modal.content.load.next();

          return modal.content.resultEmitter;
        })
      )
      .subscribe(selectedOption => {
        this.handleOperation(selectedOption);
      });
  }

  getRepositoryEntryWithPatches$() {
    return combineLatest(this.repositoryEntry$, this.patches$).pipe(
      map(([repositoryEntry, patches]) => {
        return [{ ...repositoryEntry, versions: patches }];
      })
    );
  }

  async loadDevice() {
    this.reloading = true;
    const deviceId = this.device$.value.id;
    const device = (await this.inventory.detail(deviceId, { withChildren: false })).data;
    this.device$.next(device);
    this.reloading = false;
  }

  private async handleOperation(selectedOption) {
    const operation = await this.repository.createFirmwareUpdateOperation(
      this.device$.value,
      selectedOption
    );
    this.trackOperation(operation);
  }

  private async loadOperation() {
    const deviceId = this.device$.value.id;
    const operation = await this.repository.getLastFirmwareUpdateOperation(deviceId);
    this.trackOperation(operation);
  }

  private trackOperation(operation: IOperation) {
    this.changesOperation$.next(operation);

    if (this.isInProgress(operation)) {
      this.repository.observeOperation(operation).subscribe(
        operationUpdate => {
          this.changesOperation$.next(operationUpdate);
          if (operationUpdate.status === OperationStatus.SUCCESSFUL) {
            this.loadDevice();
          }
        },
        operationUpdate => {
          this.changesOperation$.next(operationUpdate);
        }
      );
    }
  }

  private isInProgress(operation: IOperation) {
    return (
      operation && [OperationStatus.PENDING, OperationStatus.EXECUTING].includes(operation.status)
    );
  }
}
