import { Component, ViewChild } from '@angular/core';
import { IManagedObject } from '@c8y/client';
import { AlertService, gettext } from '@c8y/ngx-components';
import { uniqBy, isUndefined } from 'lodash-es';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { pipe } from 'rxjs';
import { map } from 'rxjs/operators';
import { ModalModel, RepositoryType } from '../repository.model';
import { RepositoryService } from '../repository.service';
import { PickedFiles } from '@c8y/ngx-components';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'c8y-configuration-detail',
  templateUrl: './configuration-detail.component.html'
})
export class ConfigurationDetailComponent implements ModalModel {
  @ViewChild('configurationForm', { static: true }) configurationForm: NgForm;
  selected: { id: string; name: string };
  version: string;
  description: string;
  binary: { file?: File; url?: string } = {
    file: undefined,
    url: undefined
  };
  deviceType: string;

  pattern: string = '';
  filterPipe;
  configs;
  mo: Partial<IManagedObject> = {};
  saving = false;
  uploadChoice: 'uploadBinary' | 'uploadUrl' = 'uploadBinary';
  existingBinary: File;
  submitButtonTitle: string;

  result: Promise<void> = new Promise((resolve, reject) => {
    this._save = resolve;
    this._cancel = reject;
  });

  private _save;
  private _cancel;

  constructor(
    private repositoryService: RepositoryService,
    private bsModalRef: BsModalRef,
    private alert: AlertService
  ) {}

  async ngOnInit() {
    this.configs = await this.repositoryService.listRepositoryEntries(RepositoryType.CONFIGURATION);
    if (this.selected) {
      this.uploadChoice = this.binary.file ? 'uploadBinary' : 'uploadUrl';
      this.existingBinary = this.binary.file;
    }
    this.setPipe('');
    this.submitButtonTitle = this.mo.id
      ? gettext('Update configuration')
      : gettext('Add configuration');
  }

  cancel() {
    this.bsModalRef.hide();
    this._cancel();
  }

  setPipe(filterStr: string) {
    this.pattern = filterStr;
    this.filterPipe = pipe(
      map((data: []) => uniqBy(data, 'configurationType')),
      map((data: []) => {
        return data.filter(
          (mo: any) =>
            mo.configurationType &&
            mo.configurationType.toLowerCase().indexOf(filterStr.toLowerCase()) > -1
        );
      })
    );
  }

  onFile(dropped: PickedFiles) {
    this.configurationForm.form.markAsDirty();
    if (!isUndefined(dropped.url)) {
      this.binary = {
        url: dropped.url
      };
      return;
    } else if (!isUndefined(dropped.droppedFiles)) {
      this.binary = {
        file: dropped.droppedFiles[0].file
      };
      return;
    } else {
      this.binary = {
        file: undefined,
        url: undefined
      };
    }
  }

  async save() {
    try {
      this.saving = true;
      const { selected, version, description, binary, deviceType } = this;
      if (this.existingBinary === this.binary.file) {
        binary.file = undefined;
      }
      await this.repositoryService.save(
        { selected, version, description, binary, deviceType },
        RepositoryType.CONFIGURATION,
        this.mo
      );
      this.alert.success(
        this.mo.id ? gettext('Configuration updated.') : gettext('Configuration created.')
      );
      this.bsModalRef.hide();
      this._save();
    } catch (ex) {
      this.alert.addServerFailure(ex);
      this._cancel();
    } finally {
      this.saving = false;
    }
  }
}
