import { Component, ViewChild, Output, EventEmitter } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { RepositoryService } from '../repository.service';
import { RepositoryType, ModalModel, RepositoryCategory } from '../repository.model';
import { AlertService, gettext } from '@c8y/ngx-components';
import { IManagedObject, IResultList } from '@c8y/client';
import { isUndefined } from 'lodash-es';
import { BehaviorSubject, of, from, pipe, merge, Observable } from 'rxjs';
import { BsDropdownDirective } from 'ngx-bootstrap';
import { NgForm } from '@angular/forms';
import {
  debounceTime,
  tap,
  switchMap,
  distinctUntilChanged,
  shareReplay,
  map
} from 'rxjs/operators';
import { PickedFiles } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-add-firmware-patch-modal.component',
  templateUrl: 'add-firmware-patch-modal.component.html'
})
export class AddFirmwarePatchModalComponent {
  @Output() saved: EventEmitter<RepositoryCategory> = new EventEmitter<RepositoryCategory>();

  @ViewChild('dropdown', { static: false }) dropdown: BsDropdownDirective;
  @ViewChild('firmwarePatchForm', { static: false }) form: NgForm;

  model: ModalModel = {
    selected: undefined,
    dependency: null,
    patchVersion: undefined,
    binary: {
      file: undefined,
      url: undefined
    }
  };

  firmwareInput$ = new BehaviorSubject<string>('');
  firmwares$: Observable<IResultList<IManagedObject>> = this.firmwareInput$.pipe(
    debounceTime(300),
    distinctUntilChanged(),
    switchMap(searchStr =>
      from(
        this.repository.listRepositoryEntries(RepositoryType.FIRMWARE, {
          partialName: searchStr,
          skipLegacy: true
        })
      )
    ),
    shareReplay(1)
  );
  firmwareSelected$ = new BehaviorSubject<Partial<RepositoryCategory>>(null);
  patchDependencyInput$ = new BehaviorSubject<string>('');

  saving = false;
  firmwarePreselected: boolean = false;
  baseVersions$: Observable<IResultList<IManagedObject>> = merge(
    this.firmwareInput$.pipe(
      tap(() => {
        this.model.dependency = null;
        if (this.form) {
          this.form.form.get('patchDependency').reset();
        }
      }),
      switchMap(() => of(null))
    ),
    this.firmwareSelected$
  ).pipe(
    switchMap(selectedFirmware =>
      selectedFirmware ? this.repository.listBaseVersions(selectedFirmware) : of(null)
    ),
    shareReplay(1)
  );
  baseVersionsFilterPipe = pipe(
    switchMap((data: []) =>
      this.patchDependencyInput$.pipe(
        map(partialVersion =>
          data.filter((mo: any) => {
            const version = mo.c8y_Firmware.version.toLowerCase();
            return (
              partialVersion.length === 0 || version.indexOf(partialVersion.toLowerCase()) > -1
            );
          })
        )
      )
    )
  );

  constructor(
    private modal: BsModalRef,
    private repository: RepositoryService,
    private alert: AlertService
  ) {}

  async ngOnInit() {
    this.setInitialState();
  }

  setInitialState() {
    if (this.model.selected) {
      this.firmwarePreselected = true;
      this.firmwareSelected$.next(this.model.selected);
    }
  }

  async save() {
    this.saving = true;
    this.repository
      .create(this.model, RepositoryType.FIRMWARE)
      .then(savedFirmware => {
        this.successMsg();
        this.saving = false;
        this.saved.next(savedFirmware);
        this.cancel();
      })
      .catch(e => {
        this.saving = false;
        this.saved.error(e);
        this.cancel();
      });
  }

  successMsg() {
    const msg = gettext('Firmware patch added.');
    this.alert.success(msg);
  }

  cancel() {
    this.modal.hide();
    this.saved.complete();
  }

  onFile(dropped: PickedFiles) {
    if (!isUndefined(dropped.url)) {
      this.model.binary = {
        url: dropped.url
      };
      return;
    } else if (!isUndefined(dropped.droppedFiles)) {
      this.model.binary = {
        file: dropped.droppedFiles[0].file
      };
      return;
    } else {
      this.model.binary = {
        file: undefined,
        url: undefined
      };
    }
  }
}
