import { Component, Output, Input, EventEmitter } from '@angular/core';
import { filter } from 'lodash-es';
import { OperationStatus } from '@c8y/client';
import { DeviceSoftware, DeviceSoftwareChange } from '../repository.model';

@Component({
  selector: 'c8y-device-software-list',
  templateUrl: 'device-software-list.component.html'
})
export class DeviceSoftwareListComponent {
  @Input() softwareList: DeviceSoftware[];
  @Input() deviceSoftwareChanges: DeviceSoftwareChange[];
  @Output() update = new EventEmitter<DeviceSoftware>();
  @Output() remove = new EventEmitter<DeviceSoftware>();
  showUpdate: boolean;
  showRemove: boolean;

  ngAfterContentInit() {
    this.showUpdate = this.update.observers.length > 0;
    this.showRemove = this.remove.observers.length > 0;
  }

  isSoftwareGoingToBeChanged(software: DeviceSoftware): boolean {
    const relevantChanges = filter(this.deviceSoftwareChanges, software);
    return relevantChanges.length > 0;
  }
}
